import React from 'react';

const NotFound = () => {
  return (
    <div className="not-found">
      <h2>404 - Not Found</h2>
      <p>Oops! Sorry, that ended up somewhere else. Please check the URL.</p>
    </div>
  );
}

export default NotFound;



